* {
  font-family: sans-serif;
}

.a {
  width: max-content;
  text-align: center;
  background-image: repeating-linear-gradient(to right, rgba(255, 0, 0, .4), rgba(255, 0, 0, .4) 30px, rgba(255, 0, 0, .2) 30px, rgba(255, 0, 0, .2) 60px);
  border-radius: 10px;
  align-items: center;
  padding: 20px;
  animation: hideMessage .5s linear 2s;
  display: flex;
  box-shadow: 0 0 8px #000;
}

@keyframes hideMessage {
  0% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}

.country-item {
  width: max-content;
  cursor: pointer;
  border-radius: 5px;
  padding: 5px;
  transition: all .25s linear;
}

.country-item:hover, .country-item:focus {
  scale: 1.05;
  box-shadow: 0 0 5px #000;
}

.country-info {
  width: max-content;
  height: max-content;
  text-align: center;
  border-radius: 20px;
  margin-left: auto;
  margin-right: auto;
  padding: 20px;
  box-shadow: 8px 4px 18px #000;
}

.country-info__cont {
  justify-content: center;
  align-items: center;
  display: flex;
}

.country-info__data {
  text-align: left;
}

.country-info__text {
  font-weight: 800;
}

.country-info__span {
  font-weight: 400;
}

.country-info__languages {
  padding: 0;
}

.country-info__language {
  margin-left: 25px;
}

.country-info__language:not(:last-of-type) {
  margin-bottom: 5px;
}

.country-info__picture {
  width: 250px;
  height: 250px;
  text-align: center;
  margin-left: 30px;
}

